@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates&display=swap);
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Montserrat Alternates', sans-serif;
}
body {
  scroll-behavior: smooth;
}
.container {
  margin:0 auto;
  max-width:1280px;
  width:90%
}
@media (min-width: 601px){
  .container{
    width:85%
  }
}
@media (min-width: 993px){
  .container{
    width:70%
  }
}


.navigate{width:100%;height:60px;background:#7ecbf8}.navigate__wrapper{padding:0 20px;width:100%;height:60px;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center}.navigate__logo{text-decoration:none;color:#fff;font-size:40px}.navigate__links{display:-ms-flexbox;display:flex;height:100%}.navigate__info{height:100%;padding:40% 10px;cursor:pointer;color:#fff}.navigate__link{list-style:none;height:100%;padding:0 10px}.navigate__link:hover{background:rgba(0,0,0,.1)}.navigate__link a{text-decoration:none;color:#fff;height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center}@media(max-width: 555px){.navigate__logo{font-size:20px}}
.modal{position:fixed;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,.3);z-index:100;padding:0 15px}.modal__close{position:absolute;width:30px;height:30px;background:#fff;border-radius:50%;top:-60px;left:50%;padding:5px 11px;cursor:pointer;font-weight:900}.modal__box{position:relative;max-width:450px;width:100%;min-height:100px;background:#fff;padding:20px;text-align:justify;border-radius:7px}.modal__box span{display:inline-block;width:20px}
.footer{width:100%;height:60px;background:#a0d9fa;position:relative}.footer .container{height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;color:#fff}.footer__link{text-decoration:none;color:#fff}.footer__copyright{height:45px;width:100%;position:absolute;bottom:0;background:rgba(0,0,0,.05)}
.content{min-height:calc(100vh - 120px);padding:10px 0}
.movies{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:15px;gap:15px}.movies h3{font-size:30px}.card{background:#fff;-webkit-box-shadow:2px 2px 5px 2px rgba(0,0,0,.1),-2px 0px 5px 2px rgba(0,0,0,.1);box-shadow:2px 2px 5px 2px rgba(0,0,0,.1),-2px 0px 5px 2px rgba(0,0,0,.1);border-radius:5px;-webkit-transition:-webkit-box-shadow .3s;transition:-webkit-box-shadow .3s;transition:box-shadow .3s;transition:box-shadow .3s, -webkit-box-shadow .3s;text-decoration:none;color:#222}.card:hover{-webkit-box-shadow:2px 2px 10px 5px rgba(0,0,0,.1),-2px 0px 10px 5px rgba(0,0,0,.1);box-shadow:2px 2px 10px 5px rgba(0,0,0,.1),-2px 0px 10px 5px rgba(0,0,0,.1)}.card__img{width:100%;border-top-left-radius:5px;border-top-right-radius:5px}.card__content{width:100%;padding:15px}.card__title{display:block;font-size:20px;margin-bottom:20px}.card__subtitle{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between}
.pages{margin:20px 0;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;grid-gap:10px;gap:10px;-ms-flex-wrap:wrap;flex-wrap:wrap}.pages__item{width:30px;height:30px;border:none;border-radius:2px;background:#fff;-webkit-box-shadow:1px 1px 5px rgba(0,0,0,.2);box-shadow:1px 1px 5px rgba(0,0,0,.2);cursor:pointer}.pages__item:hover{background:#fafafa;-webkit-box-shadow:2px 2px 5px rgba(0,0,0,.2);box-shadow:2px 2px 5px rgba(0,0,0,.2)}
.transition-loader{position:relative;height:30px;width:100%}.transition-loader-inner{-webkit-transform:translateY(-50%);transform:translateY(-50%);top:50%;position:absolute;width:100%;padding:0 100px;text-align:center}.transition-loader-inner label{font-size:1em;padding:1%;opacity:0;display:inline-block}.transition-loader-inner label:after{font-family:FontAwesome;content:"";width:10px;height:10px;border-radius:50%;background:#5bbdff;display:block}.transition-loader-inner label:nth-child(1){-webkit-animation:loader 3s 600ms infinite ease-in-out;animation:loader 3s 600ms infinite ease-in-out}.transition-loader-inner label:nth-child(2){-webkit-animation:loader 3s 500ms infinite ease-in-out;animation:loader 3s 500ms infinite ease-in-out}.transition-loader-inner label:nth-child(3){-webkit-animation:loader 3s 400ms infinite ease-in-out;animation:loader 3s 400ms infinite ease-in-out}.transition-loader-inner label:nth-child(4){-webkit-animation:loader 3s 300ms infinite ease-in-out;animation:loader 3s 300ms infinite ease-in-out}.transition-loader-inner label:nth-child(5){-webkit-animation:loader 3s 200ms infinite ease-in-out;animation:loader 3s 200ms infinite ease-in-out}.transition-loader-inner label:nth-child(6){-webkit-animation:loader 3s 100ms infinite ease-in-out;animation:loader 3s 100ms infinite ease-in-out}@keyframes loader{0%{opacity:0;-webkit-transform:translateX(-300px) scale(1);transform:translateX(-300px) scale(1)}33%{opacity:1;-webkit-transform:translateX(0px) scale(2);transform:translateX(0px) scale(2)}66%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}100%{opacity:0;-webkit-transform:translateX(300px) scale(2);transform:translateX(300px) scale(2)}}@-webkit-keyframes loader{0%{opacity:0;-webkit-transform:translateX(-300px)}33%{opacity:1;-webkit-transform:translateX(0px)}66%{opacity:1;-webkit-transform:translateX(0px)}100%{opacity:0;-webkit-transform:translateX(300px)}}
.search{width:100%;margin:10px 0}.search__input{width:calc(100% - 100px);border:none;border-bottom:1px solid #5faafff8;outline:none;-webkit-transition:.2s;transition:.2s;margin-bottom:2px;padding:3px 10px;font-size:20px}.search__input:focus{border-bottom:3px solid #3e97fdf8;margin-bottom:0px}.search__button{width:90px;height:30px;margin-left:5px;border:none;background:#64c6ff;font-size:18px;border-radius:3px;-webkit-box-shadow:2px 2px 10px rgba(0,0,0,.2);box-shadow:2px 2px 10px rgba(0,0,0,.2);cursor:pointer;-webkit-transition:.1s;transition:.1s}.search__button:hover{-webkit-box-shadow:2px 2px 12px rgba(0,0,0,.4);box-shadow:2px 2px 12px rgba(0,0,0,.4)}.search__button:active{-webkit-box-shadow:2px 2px 10px rgba(0,0,0,.2);box-shadow:2px 2px 10px rgba(0,0,0,.2)}.search__radioGroup{margin-top:10px}.search__radioGroup-item{margin-right:10px}.search__radioGroup-item span{margin-left:5px}
